export let sectorData = [
    {
        name: '全球攻击',
        value: 0,
        color: 'rgba(134,34,247,0.30)',
        borderColor: 'rgba(134,34,247,1)',
        labelShow: true,
    },
    {
        name: '国外攻击',
        value: 0,
        color: 'rgba(2,178,239,0.30)',
        borderColor: 'rgba(2,178,239,1)',
        labelShow: false,
    },
    {
        name: '国内攻击',
        value: 0,
        color: 'rgba(40,199,113,0.30)',
        borderColor: 'rgba(40,199,113,1)',
        labelShow: false,
    },
];

export function sectorFn(_data) {
    let data = [];
    data[0] = _data[0];
    data[1] = _data[1].value > _data[2].value ? _data[1] : _data[2];
    data[2] = _data[1].value > _data[2].value ? _data[2] : _data[1];

    var res = {
        legend: _data.map(i => ({
            name: i.name,
            color: i.borderColor,
            // icon: 'roundRect',
        })),
        series: []
    };
    for (let i = 0; i < data.length; i++) {
        res.series.push({
            name: '攻击来源比例',
            type: 'pie',
            minAngle: 5, //最小的扇区角度（0 ~ 360），用于防止某个值过小导致扇区太小影响交互
            clockWise: false, //顺时加载
            hoverAnimation: false, //鼠标移入变大
            hoverOffset: 0,
            radius: [65 - i * 10 + '%', 55 - i * 10 + '%'],
            // center: ['30%', '55%'],
            label: {
                show: false,
                fontSize: 12,
            },
            itemStyle: {
                label: {
                    show: false,
                },
                labelLine: {
                    show: false,
                },
                borderWidth: 5,
            },
            data: [
                {
                    total: data[0].value,
                    count: data[i].value,
                    value: (data[i].value / data[0].value) * 0.75,
                    name: data[i].name,
                    // selected: true,
                    label: {
                        color: 'rgba(0,0,0,0)',
                        fontSize: 20,
                        show: true,
                        formatter: function(data) {
                            return `${data.name}\n${
                                data.data.count
                            }(${Math.round(
                                (data.data.count / data.data.total) * 100,
                            )}%)`;
                        },
                        position: 'center',
                    },
                    itemStyle: {
                        color: data[i].color,
                        borderColor: data[i].borderColor,
                        borderWidth: 1,
                    },
                    emphasis: {
                        itemStyle: {
                            color: data[i].borderColor,
                        },
                        label: {
                            color: '#FFFFFF',
                        },
                    },
                },
                {
                    value: 1 - (data[i].value / data[0].value) * 0.75,
                    name: '',
                    itemStyle: {
                        color: 'rgba(0,0,0,0)',
                        borderWidth: 0,
                    },
                    tooltip: {
                        show: false,
                    },
                    hoverAnimation: false,
                },
            ],
        });
    }
    return res;
}

let optionData = sectorFn(sectorData);

export default {
    legend: {
        show: true,
        top: '16%',
        left: '58%',
        data: optionData.legend,
        itemWidth: 20,
        itemHeight: 15,
        zlevel: 5,
        z: 5,
        width: 50,
        padding: [0, 5],
        itemGap: 10,
        formatter: '{name}',
        selectedMode: false,
        textStyle: {
            color: 'rgba(255,255,255,1)',
        },
    },
    tooltip: {
        show: false,
    },
    series: optionData.series,
};
