<template>
  <div class="attackAnalysis">
    <TOP title="攻击态势分析" />
    <div class="row info">
      <div class="col title">
        <p>安<br />全<br />态<br />势<br />简<br />报</p>
      </div>
      <div class="col details">
        <p>
          <span>时间段：</span>
          {{ fmtDate("Y年M月D日", dataPickerVal[0]) }} 00:00:00
        </p>
        <p>
          <span>至：</span>{{ fmtDate("Y年M月D日", dataPickerVal[1]) }} 23:59:59
        </p>
        <p>
          <span>应用数：</span>
          <count-to :start-val="0" :end-val="appList.total" />个
        </p>
        <p>
          <span>拦截数：</span>
          <count-to :start-val="0" :end-val="interceptNumber" />次
        </p>
      </div>
      <div class="col app">
        <PIE3 class="pie3" key="app" title="受攻击应用" :list="appList" />
      </div>
      <div class="col num">
        <p>
          <span>黑客数：</span>
          <count-to
            :innerSpan="true"
            class="count_to"
            :start-val="0"
            :end-val="hackersNum"
            separator=""
          />
        </p>
        <p>
          <span>攻击源：</span>
          <count-to
            class="count_to"
            :innerSpan="true"
            :start-val="0"
            :end-val="attackNumber"
            separator=""
          />
        </p>
      </div>
      <div class="col attack">
        <PIE3
          class="pie3"
          key="attack"
          title="攻击态势分析"
          :list="attackList"
        />
      </div>
    </div>
    <!-- 时间段和应用 -->
    <div class="right box_num">
      <el-date-picker
        :clearable="false"
        v-model="dataPickerVal"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions"
        @change="onDataChange"
        popper-class="myDatepicker"
      >
      </el-date-picker>

      <el-popover
        placement="bottom"
        width="200"
        trigger="click"
        popper-class="appSelect"
      >
        <p v-for="(item, i) in apps" :key="item.id + i">
          <el-checkbox @change="onSelectApp(item.id)" v-model="item.checked">{{
            item.name
          }}</el-checkbox>
        </p>
        <el-button class="selectApps" slot="reference">{{
          selectText
        }}</el-button>
      </el-popover>
    </div>

    <div class="left box_num">
      <div class="title">监控攻击总数</div>
      <count-to
        class="number count_to"
        :innerSpan="true"
        :start-val="0"
        :end-val="interceptNumber"
        separator=""
      />
    </div>
    <!-- 攻击来源比例 -->
    <div class="box_sector">
      <div class="title">攻击来源比例</div>
      <v-chart class="chart" ref="sector" :options="sectorOpts" />
    </div>
    <div class="box_pie">
      <span class="title">攻击方式占比</span>
      <v-chart class="chart" ref="pie" :options="pieOpts" />
    </div>
    <div class="box_bar">
      <div class="title">高危攻击与攻击量走势</div>
      <v-chart class="chart" ref="bar" :options="barOpts" />
    </div>
    <PROGRESS class="box_progress" :app="appTop10" :city="cityTop10" />
  </div>
</template>
<script>
import empty from "@/model/chart_null.js";
import opts_pie, { fmtSeries } from "@/model/chart_pie.1.js";
import opts_sector, { sectorFn, sectorData } from "@/model/chart_sector.js";
import opts_bar from "@/model/chart_bar.js";
import { fmtNumber, fmtDate } from "@/utils/utils";

import TOP from "@/components/box_top";
import PROGRESS from "@/components/box_progress_10";
import PIE3 from "@/components/box_pie3";

import { api, asyncFn } from "@/api/api";
export default {
  name: "attackAnalysis",
  components: {
    TOP,
    PROGRESS,
    PIE3
  },
  data() {
    return {
      timer: null,
      timerPie: null,
      defaultSiteId: null, // 路由参数
      siteIds: undefined,
      interceptNumber: 0, // 拦截数
      attackNumber: 0, // 攻击来源数
      selectApp: [], // 选择的应用
      cityTop10: [],
      appTop10: [],
      selectText: "选择应用",
      dataPickerVal: [
        new Date(+new Date() - 3600 * 1000 * 24 * 30),
        new Date()
      ], // 选择的时间段
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      sectorOpts: empty,
      pieOpts: empty,
      barOpts: opts_bar,
      hackersNum: 0, // 黑客数
      apps: [], // 全部应用
      appList: {
        data: [],
        lg: 0,
        md: 0,
        sm: 0,
        lg_name: "高危应用",
        md_name: "中危应用",
        sm_name: "低危应用"
      },
      attackList: {
        lg: 0,
        md: 0,
        sm: 0,
        lg_name: "高危攻击",
        md_name: "中危攻击",
        sm_name: "低危攻击"
      },
      highAttack: 1234,
      chart: null
    };
  },
  computed: {
    startTime() {
      return fmtDate("Y-M-D", this.dataPickerVal[0]) + " 00:00:00";
    },
    endTime() {
      return fmtDate("Y-M-D", this.dataPickerVal[1]) + " 23:59:59";
    },
    highAttackArray() {
      return fmtNumber(this.highAttack, 99999999).split("");
    }
  },
  created() {
    if (this.$route.query && this.$route.query.site_id) {
      this.defaultSiteId = this.$route.query.site_id - 0;
    }
    // 获取应用列表
    asyncFn(api.getSiteInfos, {
      pageIndex: 1,
      pageSize: 99999
    }).then(i => {
      if (i) {
        let tmp = [
          {
            id: 0,
            name: "全部(默认)",
            checked: false
          }
        ];
        let hasOne = false;
        i.forEach(item => {
          if (!hasOne && this.defaultSiteId === item.site_id) hasOne = true;
          tmp.push({
            id: item.site_id,
            name: item.site_name,
            checked: this.defaultSiteId === item.site_id
          });
        });
        this.apps = tmp;
        // 是否有这个id
        if (!hasOne) {
          this.selectText = "选择应用";
          this.siteIds = undefined;
        } else {
          this.selectText = "已选1个";
          this.siteIds = "" + this.defaultSiteId;
        }
      }
      // 实时刷新 间隔周期30秒
      this.refreshAPI();
    });

    // 实时刷新 间隔周期30秒
    this.refreshAPI();
    this.timer = setInterval(this.refreshAPI, 30 * 1000);
  },
  beforeDestroy() {
    if (this.timer) clearInterval(this.timer);
    if (this.timerPie) clearInterval(this.timerPie);
    this.timer = null;
    this.timerPie = null;
  },
  methods: {
    fmtDate,
    refreshAPI() {
      // this.dataPickerVal[0] = new Date(+new Date() - 3600 * 1000 * 24 * 30);
      // this.dataPickerVal[1] = new Date();
      console.log("实时刷新 间隔周期30秒");
      // 高中低应用数
      asyncFn(api.getApplicationLevel, {
        siteIds: this.siteIds,
        startTime: this.startTime,
        endTime: this.endTime
      }).then(i => {
        this.appList = {
          total: i.highApplication + i.mediumApplication + i.lowApplication,
          lg: i.highApplication,
          md: i.mediumApplication,
          sm: i.lowApplication,
          lg_name: "高危应用",
          md_name: "中危应用",
          sm_name: "低危应用"
        };
      });
      // 黑客人数
      asyncFn(api.hackerNumber, {
        siteIds: this.siteIds,
        startTime: this.startTime,
        endTime: this.endTime
      }).then(i => (this.hackersNum = i.hackerNum));

      // 拦截数
      asyncFn(api.interceptNumber, {
        startTime: this.startTime,
        endTime: this.endTime,
        siteIds: this.siteIds
      }).then(i => (this.interceptNumber = i));
      // 饼状图 高中低危攻击数
      asyncFn(api.getAttackTypeAndLevel, {
        startTime: this.startTime,
        endTime: this.endTime,
        siteIds: this.siteIds
      }).then(i => {
        if (i) {
          this.attackList = {
            lg: i.attackLevel.high || 0,
            md: i.attackLevel.medium || 0,
            sm: i.attackLevel.low || 0,
            lg_name: "高危攻击",
            md_name: "中危攻击",
            sm_name: "低危攻击"
          };

          this.updatePie(i);
        }
      });

      // 来源数 来源比例扇形图
      asyncFn(api.attackNumber, {
        startTime: this.startTime,
        endTime: this.endTime,
        siteIds: this.siteIds
      }).then(data => {
        if (data) {
          this.attackNumber = data.lobalSourceNum || 0;
          this.updateSector({
            global: data.lobalSourceNum || 0,
            abroad: data.abroadSourceNum || 0,
            china: data.domesticSourceNum || 0
          });
        }
      });
      // 柱状图
      asyncFn(api.getHighAttackTrends, {
        startTime: this.startTime,
        endTime: this.endTime,
        siteIds: this.siteIds
      }).then(data => {
        if (data) this.updateBar(data);
      });

      // 应用TOP10
      asyncFn(api.getAttackApplicationTop, {
        startTime: this.startTime,
        endTime: this.endTime,
        siteIds: this.siteIds
      }).then(data => {
        this.appTop10 = data;
      });

      // 城市TOP10
      asyncFn(api.getAttackSourceTop, {
        startTime: this.startTime,
        endTime: this.endTime,
        siteIds: this.siteIds
      }).then(data => {
        this.cityTop10 = data;
      });
    },
    onDataChange() {
      console.log("若用户选择了时间及应用后则不再自动刷新数据");
      if (this.timer) clearInterval(this.timer);
      this.timer = null;
      this.refreshAPI();
    },
    onSelectApp(id) {
      console.log("若用户选择了时间及应用后则不再自动刷新数据");
      if (this.timer) clearInterval(this.timer);
      this.timer = null;
      //
      if (id === 0 && this.apps[0].checked) {
        // 选择全部
        this.apps = this.apps.map(item => {
          return {
            id: item.id,
            name: item.name,
            checked: item.id === 0
          };
        });
        this.selectText = "已选全部";
      } else {
        this.apps = this.apps.map(item => {
          return {
            id: item.id,
            name: item.name,
            checked: item.id === 0 ? false : item.checked
          };
        });
      }
      this.siteIds = this.apps
        .filter(i => i.checked === true)
        .map(i => i.id)
        .join();

      if (this.siteIds === "") {
        this.selectText = "选择应用";
        this.siteIds = undefined;
      } else if (this.siteIds === "0") {
        this.selectText = "已选全部";
        this.siteIds = undefined;
      } else this.selectText = `已选${this.siteIds.split(",").length}个`;

      this.refreshAPI(); // 刷新数据
    },
    updateBar(data) {
      let opts = JSON.parse(JSON.stringify(opts_bar));

      let dateList = []; // 时间轴
      let totalList = []; // 总量
      let highList = []; // 高危

      data.forEach(({ time, high, low, medium }) => {
        // dateList.push(fmtDate("Y.M.D", time));
        dateList.push(time);
        totalList.push(high + low + medium);
        highList.push(high);
      });

      opts.xAxis.data = dateList;

      opts.xAxis.axisLabel.rotate = data.length > 10 ? -30 : 0;

      opts.series[0].data = totalList;
      opts.series[1].data = highList;

      this.barOpts = opts;
    },
    updatePie(data) {
      if (JSON.stringify(data.attackLevel) === "{}") {
        return (this.pieOpts = empty);
      }
      let tmp = JSON.parse(JSON.stringify(opts_pie));

      tmp.series[0].data[0].value = data.attackLevel.high || 0;
      tmp.series[0].data[1].value = data.attackLevel.medium || 0;
      tmp.series[0].data[2].value = data.attackLevel.low || 0;
      tmp.series[1].data = fmtSeries(data.attackType);

      this.pieOpts = tmp;

      if (this.timerPie === null) this.$nextTick(this.animationPie);
    },
    animationPie() {
      let dataIndex = -1;
      let pie = this.$refs.pie;
      let dataLen = pie.options.series[1].data.length;
      let fn = () => {
        pie.dispatchAction({
          type: "downplay",
          seriesIndex: 1,
          dataIndex
        });
        dataIndex = (dataIndex + 1) % dataLen;
        pie.dispatchAction({
          type: "highlight",
          seriesIndex: 1,
          dataIndex
        });
        // 显示 tooltip
        pie.dispatchAction({
          type: "showTip",
          seriesIndex: 1,
          dataIndex
        });
      };
      fn();
      this.timerPie = setInterval(fn, 5 * 1000);
    },
    updateSector({ global, abroad, china }) {
      if (global === 0) return (this.sectorOpts = empty);

      let opts = JSON.parse(JSON.stringify(opts_sector));

      sectorData[0].value = global; // 全球攻击
      sectorData[1].value = abroad; // 国外攻击
      sectorData[2].value = china; // 国内攻击

      let obj = sectorFn(sectorData);
      opts.legend.data = obj.legend;
      opts.series = obj.series;

      this.sectorOpts = opts;

      // 默认高亮
      let chart = this.$refs.sector.chart;

      chart.dispatchAction({
        type: "highlight",
        seriesIndex: 0
      });
      chart.on("mousemove", "series", function(obj) {
        chart.dispatchAction({
          type: "downplay",
          seriesIndex: [0, 1, 2, 3, 4, 5, 6]
        });
        chart.dispatchAction({
          type: "highlight",
          seriesIndex: obj.seriesIndex
        });
      });
    }
  }
};
</script>

