<template>
  <div class="box_progress">
    <span class="title">遭受攻击{{ isCity ? "城市" : "站点" }} TOP10</span>
    <div class="btns">
      <span :class="isCity && 'active'" @click="isCity = true">
        城市
      </span>
      <span :class="!isCity && 'active'" @click="isCity = false">
        站点
      </span>
    </div>
    <div class="content">
      <div class="roll">
        <div v-for="(item, index) in list" :key="item.name + index" class="row">
          <p>
            <span>{{ item.name === "" ? "未知" : item.name }}</span>
            <count-to :start-val="0" :end-val="item.value" />
          </p>
          <div class="progress">
            <div
              class="progress_inner"
              :style="{ width: `${(item.value / list[0].value) * 100}%` }"
            ></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["app", "city"],
  name: "box_progress",
  data() {
    return {
      isCity: true // IP APP
    };
  },
  computed: {
    list() {
      return this.isCity ? this.city : this.app;
    }
  }
};
</script>

