<template>
  <div class="list">
    <div class="item">
      <div class="name lg">{{ list.lg_name }}</div>
      <v-chart class="pie" ref="pie_lg" :options="pieOpts_lg" />
      <div class="num lg"><count-to :start-val="0" :end-val="list.lg" /></div>
    </div>
    <div class="item">
      <div class="name md">{{ list.md_name }}</div>
      <v-chart class="pie" ref="pie_md" :options="pieOpts_md" />
      <div class="num md"><count-to :start-val="0" :end-val="list.md" /></div>
    </div>
    <div class="item">
      <div class="name sm">{{ list.sm_name }}</div>
      <v-chart class="pie" ref="pie_sm" :options="pieOpts_sm" />
      <div class="num sm"><count-to :start-val="0" :end-val="list.sm" /></div>
    </div>
  </div>
</template>
<script>
import opts_pies from "@/model/chart_pie_level.js";
export default {
  props: {
    list: {
      type: Object
    },
    title: {
      type: String,
      default: "占比关系图"
    }
  },
  data() {
    return {
      pieOpts_lg: opts_pies,
      pieOpts_md: opts_pies,
      pieOpts_sm: opts_pies
    };
  },
  watch:{
    'list': function(){
      this.initPie3();
    }
  },
  methods: {
    initPie3() {
      let lg = JSON.parse(JSON.stringify(opts_pies));
      lg.series[0].data[0].value = this.list.lg; // 高危
      lg.series[0].data[1].value = this.list.md + this.list.sm; // 其他
      lg.series[0].data[0].itemStyle.color = "rgba(241, 25, 56, 0.5)";
      lg.series[0].data[0].itemStyle.borderColor = "rgba(241, 25, 56, 1)";
      lg.series[0].data[0].label["color"] = "rgba(241, 25, 56, 1)";
      this.$refs.pie_lg.clear()
      this.pieOpts_lg = lg;

      let md = JSON.parse(JSON.stringify(opts_pies));
      md.series[0].data[0].value = this.list.md; // 中危
      md.series[0].data[1].value = this.list.lg + this.list.sm; // 其他
      md.series[0].data[0].itemStyle.color = "rgba(250, 116, 26, 0.5)";
      md.series[0].data[0].itemStyle.borderColor = "rgba(250, 116, 26, 1)";
      md.series[0].data[0].label["color"] = "rgba(250, 116, 26, 1)";
      this.$refs.pie_lg.clear()
      this.pieOpts_md = md;

      let sm = JSON.parse(JSON.stringify(opts_pies));
      sm.series[0].data[0].value = this.list.sm; // 低危
      sm.series[0].data[1].value = this.list.lg + this.list.md; // 其他
      sm.series[0].data[0].itemStyle.color = "rgba(255, 196, 27, 0.5)";
      sm.series[0].data[0].itemStyle.borderColor = "rgba(255, 196, 27, 1)";
      sm.series[0].data[0].label["color"] = "rgba(255, 196, 27, 1)";
      this.$refs.pie_lg.clear()
      this.pieOpts_sm = sm;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/style/mixin.scss";

.list {
  width: 100%;
  .item {
    display: inline-block;
    width: 100px;
  }
  .pie {
    width: 100px;
    height: 100px;
  }
  .name,
  .num {
    font-size: 18px;
    text-align: center;
  }
}
</style>

